@import '../../../../styles/variables' ;
@import '../../../../styles/typography' ;

.global-header {
    margin: 0 auto;
    
    padding-top: 15px;
    width: 100%;
    display: flex;
    flex-direction: none;
    justify-content: space-between;
    align-items: center;

    &__info {
        display: flex;
        flex-direction: none
    }

    &__title {
        @extend .text__bold;
        color: $base-text;
        margin: 0;
    }

    &__supply {
        margin: 0 10px;
        @extend .text__bold;
        color: $link-color;
    }

    &__parameters {
        width: 35%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }

    &__parameter {
        @extend .text__bold;
        color: $link-color;
        cursor: pointer;
        margin-left: 35px !important;
        
        &--reset {
        }
        
        &--back {
        }
    }
}
