.gradient__container,
.monohrom__container {
    min-width: 100%;
    min-height: 100vh;
    /* margin: 25px; */
    display: flex;
    justify-content: center;
    align-items: center;

}

.monohrom__container {
    background: rgb(255, 255, 255);
    background: linear-gradient(90deg, rgba(255, 255, 255, 0.822) 0%, rgba(255, 255, 255, 0.384) 48%, rgb(255, 255, 255) 100%);
    animation: gradient 2s ease infinite;
    background-size: 5000% 5000%;
    color: rgb(0, 0, 0);
}

.gradient__container {
    background-color: #FF3CAC;
    background-image: linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #4bafff 100%);
    animation: gradient 7s ease infinite;
    background-size: 5000% 5000%;
    color: rgb(255, 255, 255);
}



@keyframes gradient {

    0% {
        background-position: 0 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0 50%;
    }
}

.children__wrapper {
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(255, 255, 255);
    border-radius: 50%;
    /* border: 1px solid; */
}