@import '../../../styles/mixins';
@import '../../../styles/variables';
@import '../../../styles/typography';

.global__container {
    min-height: 100%;
    margin: 0;
    padding: 0;
    font-size: large;
    // @include gradient();
    background-color: rgb(255, 255, 255);
}

.select__wrapper {
    width: 300px;
}


.global__main {
    margin: 0 auto;
    height: 85vh;
    // width: $global-continer;
    @include flexbox($direction: column,
        $justify: center,
        $align-items: center);
}

.global__result,
.global__selects {
    margin: 0 5px;
    color: black;
    min-height: 100%;
    width: 27%;
    // max-width: 250px;
    @include flexbox($direction: column,
        $justify: center);
}

.select__wrapper,
.global__value,
.global__value--error {
    @extend .text__big;
    margin: 10px 0;
    min-width: 50px;
    // width: 10vw;
    max-width: 100%;
    height: 50px;
    text-align: start;
    @include flexbox($align-items: end);
}

.global__value {
    color: $black-color;
}

.global__value--error {
    color: $black-color;
}

.global__header, .global__menu {

    width: $internallyGlobal;
    max-width: $internally2Global;
    min-width: 390px;
}


.global__header {
    padding-right: 3px;
    display: flex;
    font-display: row;
    justify-content: space-between;
    align-items: center;
    
}

.glabal__appName {
    @extend .text__big;
    color: $blue-color;
    @media screen and (max-width: 450px) {
       display: none;
    }
}

.global__logo {
    display: block;
    height: 60px;
    width: 140px;
}

.global__menu {
    margin-bottom: 50px;
    height: 300px;

    @include flexbox($justify: space-around,
        $align-items: center);


    background: rgb(255, 255, 255);
    border-radius: $border-big;
    border: 2px solid $blue-color;
}

.global__button {
    height: 100%;
    // width: 100px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: end;
    flex-wrap: nowrap;
}

.button__wrapper {
    width: 100px;
    height: 40px;
    margin-bottom: 20px;
    margin-right: 5px;
}