@import '../../../styles/typography';

.result__container {
   
    margin-top: 4vh;
    margin-right: 7px;
    width: 355px;
   
    max-height: 100%;
    padding: 2vh;
    position: sticky;
    top: 15vh;
    right: 0vh;

    border: 1px solid rgb(160, 179, 179) !important;
    border-radius: 3px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    transition-property: background-color, top, bottom, left, right, height, display, position !important;
    transition-duration: 5.5s !important;
    transition-delay: 0.5s;

}

.base__title, .description__title {
    display: flex;
    justify-content: space-between;
    // transition-property: background-color, top, bottom, left, right, height, display !important;
    // transition-duration: 5.5s !important;
    // transition-delay: 0.5s;
}

.lt__title {
    display: flex;
    justify-content: space-between;
    // transition-property: background-color, top, bottom, left, right, height, display !important;
    // transition-duration: 5.5s !important;
    // transition-delay: 0.5s;
}

.base__title,
.lt__title,
.description__title {
    margin: 2%;
}

.result__title {
    @extend .text;
}

.result__value--selected {
    @extend .text__bold;
    color: $black-text;
}

.result__value--error {
    @extend .text__bold;
    color: $error-text;
}

.description__title{
    @extend .text__bold;
    color: $link-color;
    cursor: pointer;
}


.information {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;

}




@media (max-width: 778px) {

    .result__container {
        width: 100%;
        min-height: 10px;
        height: auto;
        padding: 2vh;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        margin-right: 0vh !important;

    }

    .information {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex-wrap: wrap;

    }


    .base__title,
    .lt__title {
        margin: 1px;
        font-size: small;

    }

}

@media (max-width: 680px) {

    .result__container {
        /* width: 75vh; */
        min-height: 10px;
        height: auto;
        padding: 2vh;
        /* position: sticky;
        right: 0vh;
        left: 0vh;
        bottom: 50vh; */
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        margin-right: 0vh !important;

    }

    .information {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex-wrap: wrap;

    }



    .base__title {
        margin: 1px;
        font-size: small;
    }

    .lt__title {
        display: none !important;
    }

}