
html,
body {
    height: 100%;
    margin: 0;
    padding: 0;
}

.deal__container {

    height: 100vh; /* Измените значение высоты на 100vh */
    width: 100%;
    display: flex;
    flex-direction: column;
    
   

}

.deal__global {
    margin-left: 1%;
    width: 98%; /* Измените значение ширины на 100% */
    height: 45px;
    text-align: center;
    line-height: 45px; /* Исправьте значение line-height на 45px */
}

.deal__main {
    display: flex;
    flex-grow: 1;
    overflow-y: auto;
}

.deal-settings {

    width: 25%;
    height: 100%;
    padding: 20px;
   
}

.deal-settings__item {
    width: 100%;
    max-height: 50px !important;
    margin-bottom: 30px;
}




.deal-od {
    width: 100%;
}

.deal-fields {

    display: flex;
    width: 75%;
    height: 100%;
    overflow: hidden;
   

}

.deal__footer {

    /* display: flex; */
    /* justify-content: space-between; Добавьте свойство justify-content */
    padding: 10px 20px; /* Добавьте немного отступов */
   
}

.deal-price__container {}

.deal-buttons__container {

    margin-top: 10px;
    display: flex;
    justify-content: end;
    align-items: flex-end;
}