@import '../../../styles/variables';

.konstructor__wrapper{
    max-height: 100vh;
    // min-width: 2200px;
    // min-width: $external;
    /* background-color: rgb(255, 255, 255) !important; */
    // color: black !important;
    overflow: auto;
    display: flex;
    justify-content: center;
 
    

}

.main {
    height: 100%;
    width: $external;
    display: grid;
    position: relative;
    margin: 0;
    grid-template-columns: 3fr 1fr;
    grid-template-rows: 1fr ;
 
    grid-gap: 0px;
    grid-auto-rows: minmax(2px, auto);

}

.complect {
    margin: 0 auto !important;
    // min-width: 100vw;
    width: $internally;
    background-color: $background-color;
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 1;
   

}

.infoblocks {
    margin: 0 auto;
   
    width: $internally2;
    // margin-left: 3vh;
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 2;
    align-self: start;



}

.result__container {
    /* height: 100%; */
    /* width: 0; */
    /* background-color: rgb(0, 141, 146); */
    grid-column-start: 2;
    grid-column-end: 2;
    /* justify-self: end; */
    align-self: start;
    justify-items: end;

    align-items: end;
    /* grid-row: 2; */
}

@media (max-width: 780px) {
    .main {
        height: 100%;
        display: grid;
        position: relative;
        margin: 0;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
        grid-gap: 24px;
        grid-auto-rows: minmax(12px, auto);

    }

    .complect {
        /* background-color: aquamarine; */
        grid-column-start: 1;
        grid-column-end: 1;
        grid-row-start: 1;
        grid-row-end: 1;
        // margin-left: 2vh;
        // width: 100%;
        /* justify-self: center;
        width: 90%;
        position: sticky;
        top: 1vh;
        z-index: 1000 !important; */
    }

    .infoblocks {
        margin-left: 0vh;
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 2;
        grid-row-end: 2;
        justify-content: end;
        justify-self: normal;
        

    }

    .result__container {
        grid-column-start: 1;
        grid-column-end: 1;
        grid-row-start: 3;
        grid-row-end: 3;
        justify-self: center;
    }

}

@media (max-width: 700px) {

    .complect {
     
        position: sticky;
        top: 0vh;
        z-index: 1000 !important;
        // width: 85vh;
        margin: 0;
        padding: 0;
        width: 100%;
    }


}
@media (max-width: 520px) {
    .main {
        display: grid;
        position: relative;
        margin: 0;
        grid-template-columns: 1fr;
        grid-template-rows: 3fr;
        grid-gap: 0vh;
        max-width: 100%;

    }

    .complect {
        /* background-color: blueviolet; */
        grid-column-start: 1;
        grid-column-end: 1;
        grid-row-start: 1;
        grid-row-end: 1;
        margin-left: 0vh;
        display: flex;
        justify-self: center;
        justify-items: center;
        
        position: sticky;
        top: 0vh;
        left: 0;
        right: 0;
        z-index: 1000 !important;
        /* width: 150px; */
        margin: 0;
        padding: 0;
        max-width: 320px;
     
    }

    .infoblocks {
        margin-left: 0vh;
        grid-column-start: 1;
        grid-column-end: 1;
        grid-row-start: 2;
        grid-row-end: 2;
        max-width: 100%;

    }

    .result__container {
        position: sticky;
        bottom: 0;
        margin: 0;
        padding: 0;
        grid-column-start: 1;
        grid-column-end: 1;
        grid-row-start: 3;
        grid-row-end: 3;

    }

}





@media (max-width: 400px) {
    .complect {  
        max-width: 390px; 
    }
}



@media (max-width: 375px) {
    .complect {  
        max-width: 375px; 
    }
}
@media (max-width: 322px) {
    .complect {  
        max-width: 320px; 
    }
}


